body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  color:green;
  font-style:italic;
}

li {
  color: grey;
  padding-top: 3px;
  font-size: 15px;
  list-style-type: none;
}

.error{
  color:red;
  font-size: 15px;
  font-style: italic;
  list-style-type:none;
  background:gray;
  border-style: solid;
  border-radius:5px;
}

.success{
  padding:3px;
  background:lightgreen;
  border-style:solid;
  border-radius:4px;
  color:green;
  font-size:15px;
  font-style: italic;
  list-style-type: none;
}
